import type { ComponentType } from 'react';

/**
 * Утилита для получения displayName компонента.
 * Если у компонента нет displayName, будет использовано значение name полученного компонента.
 * При получении анонимной функции будет возвращено значение 'Component'.
 * @param {ComponentType} Component - компонент, для которого необходимо полчить displayName.
 */
function getDisplayName<T extends Record<string, unknown>>(Component: ComponentType<T>): string {
  return Component.displayName || Component.name || 'Component';
}

export default getDisplayName;
